.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
  display: flex;
  justify-content: space-between;
  background-color: #333;
  padding: 1rem;
}

ul {
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
}

li {
  margin-right: 2rem;
}

a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  color: #ffcc00;
}

/* styles forms*/
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 150px;
}

form {
  max-width: 400px;
  margin: 0 auto;
  padding: 30px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
}

form h2 {
  text-align: center;
  margin-bottom: 40px;
}

form h5 {
  text-align: center;
}

form div {
  display: flex;
  align-items: center;
  margin: 10px 0;
  padding-right: 10px;
}

form div label {
  width: 100px;
  margin-right: 10px;
  text-align: right;
}

form div input {
  flex: 1;
  min-width: 200px;
  width: calc(100% - 120px);
}

.button-container {
  display: flex;
  justify-content: center;
}

.button-container button {
  padding: 8px 40px;
  font-size: 16px;
  margin-top: 10px;
}
/* styles forms*/

/* styles for modals */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.notification-success {
  background-color: #efd6bd;
  color: #cc8259;
  padding: 2rem;
  border: 1px solid #cc8259;
  border-radius: 0.5rem;
  max-width: 500px;
  width: 90%;
  text-align: center;
  position: relative;
}

.close-btn-success {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background: transparent;
  border: none;
  color: #7e4f1a;
  font-size: 1.5rem;
  cursor: pointer;
}
/* styles for modals */

.Product {
  flex: 1;
  padding: 20px;

  text-align: center;
}

.navbar-welcome {
  color: #fff;
}

.logout-link-modal {
  margin: 0 1rem 0 1rem;
  color: blue;
  font-weight: normal;
  text-decoration: underline;
}

/* LandingPage */

.landing-page {
  font-family: Arial, sans-serif;
  color: #333;
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 20px;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
}

.hero-section {
  background-color: #4CAF50;
  color: white;
  padding: 15px;
  text-align: center;
  margin: 15px 0;
  flex-grow: 1;
}

.welcome-section, .topics-covered, .additional-resources {
  margin: 15px 0;
}

.welcome-section h2, .topics-covered h2, .additional-resources h2 {
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: bold;
  position: relative;
  padding-bottom: 8px;
}

.welcome-section h2::after,
.topics-covered h2::after,
.additional-resources h2::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #4CAF50;
  bottom: 0;
  left: 0;
}

.topics-covered {
  min-height: 250px;
}

.topics-covered .topics-grid {
  display: flex;
  justify-content: space-between;
  gap: 15px;
  text-align: left;
}

.topics-covered .topics-grid div {
  flex: 1;
  padding: 8px;
  font-size: 14px;
}

.topics-covered .topics-grid div strong {
  display: block;
  margin-bottom: 4px;
}

.footer {
  text-align: center;
  margin-top: auto;
  padding: 8px;
  background-color: #f5f5f5;
  position: relative;
  bottom: 0;
  width: 100%;
  font-size: 12px;
}
